import classes from "./GearDatabaseList.module.css"
import Select, { createFilter } from "react-select"
import lang from "../../assets/json/language/EUen.json"
import gearHead from "../../assets/json/parameters/GearInfoHead.json"
import gearClothes from "../../assets/json/parameters/GearInfoClothes.json"
import gearShoes from "../../assets/json/parameters/GearInfoShoes.json"
import { useLocalStorage } from "../../Utils"

const GearDatabaseList = props => {
    const [gearList] = useLocalStorage("gearList", {
        HaveGearHeadMap: {},
        HaveGearClothesMap: {},
        HaveGearShoesMap: {},
    })
    const centerStyle = {
        singleValue: (styles, { data }) => ({ ...styles, display: "flex", alignItems: "center" }),
    }

    const getGearLabel = (gear, type) => {
        const langList =
            type === 0
                ? lang["CommonMsg/Gear/GearName_Head"]
                : type === 1
                ? lang["CommonMsg/Gear/GearName_Clothes"]
                : lang["CommonMsg/Gear/GearName_Shoes"]
        return langList[gear.__RowId.substring(4)]
    }

    const getGearEntries = () => {
        const entries = []

        for (const [key, val] of Object.entries(gearList.HaveGearHeadMap)) {
            if (props.filterFunction(val)) {
                const pgear = gearHead.filter(elem => elem.Id === parseInt(key))
                const entry = {
                    Id: parseInt(key),
                    label: getGearLabel(pgear[0], 0),
                    ImgName: pgear[0].__RowId,
                    value: pgear[0],
                    Seed: val.RandomContext,
                    OriginalEntry: val,
                    Type: 0,
                }
                entries.push(entry)
            }
        }
        for (const [key, val] of Object.entries(gearList.HaveGearClothesMap)) {
            if (props.filterFunction(val)) {
                const pgear = gearClothes.filter(elem => elem.Id === parseInt(key))
                const entry = {
                    Id: parseInt(key),
                    label: getGearLabel(pgear[0], 1),
                    ImgName: pgear[0].__RowId,
                    value: pgear[0],
                    Seed: val.RandomContext,
                    OriginalEntry: val,
                    Type: 1,
                }
                entries.push(entry)
            }
        }
        for (const [key, val] of Object.entries(gearList.HaveGearShoesMap)) {
            if (props.filterFunction(val)) {
                const pgear = gearShoes.filter(elem => elem.Id === parseInt(key))
                const entry = {
                    Id: parseInt(key),
                    label: getGearLabel(pgear[0], 2),
                    ImgName: pgear[0].__RowId,
                    value: pgear[0],
                    Seed: val.RandomContext,
                    OriginalEntry: val,
                    Type: 2,
                }
                entries.push(entry)
            }
        }
        return entries
    }

    const entries = getGearEntries()

    const filterConfig = {
        ignoreCase: true,
        ignoreAccents: true,
        trim: true,
        matchFrom: "any",
    }

    return (
        <div className={classes.SettingsContent}>
            <p>If you have gear in your database, you can select it here to preset details.</p>
            <p>
                <strong>Gear:</strong>
            </p>
            <Select
                isSearchable
                filterOption={createFilter(filterConfig)}
                value={props.currentGear}
                onChange={props.gearChange}
                options={entries}
                styles={centerStyle}
                formatOptionLabel={elem => {
                    if (elem.ImgName === undefined) {
                        return "None"
                    }
                    const url = `https://leanny.github.io/splat3/images/gear/${elem.ImgName}.png`
                    return (
                        <>
                            <img src={url} width="32" alt={elem.label} />
                            <span className={classes.Gap}> {elem.label}</span>
                        </>
                    )
                }}
            />
        </div>
    )
}

export default GearDatabaseList
