import React from "react"
import classes from "./ResultSet.module.css"
import useCollapse from "react-collapsed"
import { get_roll, advanceFrame } from "../SeedChecker/SeedCalculator"
import SkillRev from "../../assets/json/parameters/SkillIdsRev.json"

const ResultSetSearch = props => {
    const { getCollapseProps, getToggleProps, isExpanded } = useCollapse()

    const format_res = (r, r2, r3) => {
        if (r3 === "Unknown") {
            if (r === "None") {
                return (
                    <>
                        Unlock a slot with NO drink active to unlock{" "}
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${r2}.png`}
                            alt={r[1]}
                            className={classes.Ability}
                            width={32}
                        />
                    </>
                )
            } else {
                return (
                    <>
                        Unlock a slot with{" "}
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${r}.png`}
                            alt={r[1]}
                            className={classes.Ability}
                            width={32}
                        />{" "}
                        drink active to unlock{" "}
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${r2}.png`}
                            alt={r[1]}
                            className={classes.Ability}
                            width={32}
                        />
                    </>
                )
            }
        } else {
            return (
                <>
                    Unlock a slot with{" "}
                    <img
                        src={`https://leanny.github.io/splat3/images/skill/${r3}.png`}
                        alt={r[1]}
                        className={classes.Ability}
                        width={32}
                    />{" "}
                    chunks.
                </>
            )
        }
    }

    const get_shortest_advances = (start_seed, end_seed, brand) => {
        const limit = advanceFrame(end_seed >>> 0, 1) >>> 0
        const all_paths = []
        let current_seed = start_seed
        const drink_use = SkillRev[0]
        for (let i = 0; current_seed !== end_seed && current_seed !== limit; i++) {
            const curr = []
            for (let k = 0; k < i; k++) {
                curr.push("None")
            }
            current_seed = advanceFrame(start_seed, i) >>> 0
            while (current_seed !== end_seed && current_seed !== limit) {
                curr.push("Drink")
                const [res, ,] = get_roll(current_seed, brand, drink_use)
                if (res === 0) return []
                current_seed = res >>> 0
            }
            if (current_seed === end_seed) {
                all_paths.push(curr)
            }
            current_seed = advanceFrame(start_seed, i) >>> 0
        }
        for (let i = 0; current_seed !== end_seed && current_seed !== limit; i++) {
            const curr = []
            current_seed = advanceFrame(start_seed, i) >>> 0
            for (let k = 0; k < i; k++) {
                curr.push("Drink")
                const [res, ,] = get_roll(current_seed, brand, drink_use)
                if (res === 0) return []
                current_seed = res >>> 0
            }
            while (current_seed !== end_seed && current_seed !== limit) {
                curr.push("None")
                current_seed = advanceFrame(current_seed, 1) >>> 0
                if (current_seed === 0) return []
            }
            if (current_seed === end_seed) {
                all_paths.push(curr)
            }
            current_seed = advanceFrame(start_seed, i) >>> 0
        }
        return all_paths.reduce(
            function (p, c) {
                return p.length > c.length ? c : p
            },
            { length: Infinity }
        )
    }

    const format_shortest_path = (start_seed, drink, brand, path) => {
        const res = []
        let current_seed = start_seed >>> 0
        const use_drink = drink === "None" || drink === "Unknown" ? SkillRev[0] : drink
        let i = 0
        for (const elem of path) {
            if (elem === "None") {
                const [current_seed_res, ability_rolled] = get_roll(current_seed, brand, "None")
                current_seed = current_seed_res
                res.push(
                    <li key={i}>
                        Unlock one slot with NO drink active to unlock a slot with{" "}
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${ability_rolled}.png`}
                            alt={ability_rolled}
                            className={classes.Ability}
                            width={32}
                        />{" "}
                        (Resulting Seed: 0x{(current_seed >>> 0).toString(16)}).
                    </li>
                )
            } else {
                const [current_seed_res, ability_rolled] = get_roll(current_seed, brand, use_drink)
                current_seed = current_seed_res
                res.push(
                    <li key={i}>
                        Unlock one slot with{" "}
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${use_drink}.png`}
                            alt={use_drink}
                            className={classes.Ability}
                            width={32}
                        />{" "}
                        drink active to unlock a slot with{" "}
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${ability_rolled}.png`}
                            alt={ability_rolled}
                            className={classes.Ability}
                            width={32}
                        />{" "}
                        (Resulting Seed: 0x{(current_seed >>> 0).toString(16)}).
                    </li>
                )
            }
            i += 1
        }
        return res
    }

    const efficient_collapse = (start_seed, end_seed, drink, brand) => {
        const shortest_path = get_shortest_advances(start_seed, end_seed, brand)
        return (
            <>
                <p>
                    Note: You can use every drink instead of the suggested one. Please refer to the usual table with
                    your desired drink active to see exact abilities.
                </p>
                <ol>{format_shortest_path(start_seed >>> 0, drink, brand, shortest_path)}</ol>
            </>
        )
    }

    if (props.results.length === 0) {
        return <div className={classes.WrapperNoScroll}>No results to display.</div>
    }
    if (props.results[0].length === 1) {
        return <div className={classes.WrapperNoScroll}>No results found.</div>
    }
    const all_res = props.results.map((arr, idx) => {
        const seed = arr[0]
        const res = arr[1]
        const advances = arr[2]
        return (
            <div key={idx}>
                Path found:
                <ol>
                    <li>
                        <p>
                            {`Advance your seed ${advances} times and start at seed 0x${(seed >>> 0).toString(16)}`}.
                            Make sure the gear has no slots on it.
                        </p>
                        {advances === 0 ? null : (
                            <>
                                <button {...getToggleProps()}>{isExpanded ? "Hide" : "Show how to advance"}</button>
                                <section {...getCollapseProps()}>
                                    {efficient_collapse(
                                        props.currentSeed >>> 0,
                                        seed >>> 0,
                                        res[0][0],
                                        props.currentBrand
                                    )}
                                </section>
                            </>
                        )}
                    </li>
                    <li>{format_res(res[0][0], res[1][0], res[2][0])}</li>
                    <li>{format_res(res[0][1], res[1][1], res[2][1])}</li>
                    <li>{format_res(res[0][2], res[1][2], res[2][2])}</li>
                </ol>
            </div>
        )
    })
    return <div className={classes.WrapperNoScroll}>{all_res}</div>
}

export default React.memo(ResultSetSearch)
