import { useState, useCallback } from "react"
import SkillIds from "./assets/json/parameters/SkillIds.json"

export const useLocalStorage = (storageKey, fallbackState) => {
    const [value, _setValue] = useState(JSON.parse(localStorage.getItem(storageKey)) ?? fallbackState)

    const setValue = useCallback(
        value => {
            _setValue(value)
            localStorage.setItem(storageKey, JSON.stringify(value))
        },
        [storageKey]
    )

    return [value, setValue]
}

export const getSkillId = name => {
    if (name === "Unknown") return 255
    return SkillIds[name]
}

export const getSkillName = id => {
    if (id === 255) return "Unknown"
    for (const [k, v] of Object.entries(SkillIds)) {
        if (v === id) return k
    }
}

export const getName = (langList, gear) => langList[gear.__RowId.substring(4)]

export const skillImage = name => `https://leanny.github.io/splat3/images/skill/${name}.png`
export const brandImage = name => `https://leanny.github.io/splat3/images/brand/${name}.png`
export const gearImage = name => `https://leanny.github.io/splat3/images/gear/${name}.png`
