import Input from "../../components/UI/Input/Input"
import classes from "./Controls.module.css"

const RefineMax = props => {
    return (
        <div className={classes.Wrapper}>
            <p>
                The refine count is used for two things: Tell the search to stop when you exceed the refine count (so
                the lower the number, the fast you might see a result when there are too many candidates), as well as
                the amount of memory consumption the application uses in order to allow you to refine the result.
            </p>
            <p>
                When refining a seed, the application will not look at every possible seed, but instead only look at the
                seeds it has in memory. This will speed up additional searches by a lot (i.e. from minutes it goes to
                down to a few seconds at most). If you are on a mobile phone, my suggestion is not to succeed the refine
                count by more than 100.000 (100k).
            </p>
            <Input title="Refine Count" type="number" val={props.value} changed={props.refineHandler} />
        </div>
    )
}

export default RefineMax
