import React from "react"
import Input from "../../components/UI/Input/Input"
import classes from "./Controls.module.css"
import ability_list from "../../assets/json/parameters/SkillIds.json"
import ability_list_rev from "../../assets/json/parameters/SkillIdsRev.json"
import Select from "react-select"

const drinks = [
    { value: "Action_Up", label: "Actionberry Twist" },
    { value: "SquidMove_Up", label: "Cherry Apple Fizz" },
    { value: "JumpTime_Save", label: "Citrus Hip-Hopper" },
    { value: "InkRecovery_Up", label: "Ink-Well Chiller" },
    { value: "SpecialIncrease_Up", label: "Ma's Special Blend" },
    { value: "MainInk_Save", label: "Main Mega Pucker-Up" },
    { value: "RespawnTime_Save", label: "Matcha Reviver" },
    { value: "RespawnSpecialGauge_Save", label: "Salty Melon Special" },
    { value: "SpecialSpec_Up", label: "Smoothie Especial" },
    { value: "HumanMove_Up", label: "Speedy Gazpacho" },
    { value: "OpInkEffect_Reduction", label: "Stompin' Grape" },
    { value: "SubInk_Save", label: "Sub Berry Blaster Blitz" },
    { value: "SubEffect_Reduction", label: "Sub Lime" },
    { value: "SubSpec_Up", label: "Super Sub Juicer" },
]

const ControlsSearch = props => {
    return (
        <div className={classes.Wrapper}>
            <Input title="Current Seed" changed={props.seedValidator} val={props.currentSeed} />
            <Input type="number" title="Start Slot" val={props.currentStartFrame} changed={props.startValidator} />
            <Input
                type="number"
                title="Maximum Number of Slots to Search"
                val={props.currentNumberOfFrames}
                changed={props.numberValidator}
            />

            <div className={classes.InputWrapper}>
                <span className={classes.Title}>Allowed Drinks</span>
                <div className={classes.DrinkSlots}>
                    <Select
                        defaultValue={drinks}
                        isMulti
                        name="drinks"
                        options={drinks}
                        onChange={props.handleDrinkSelection}
                        value={props.selectedDrinks}
                        formatOptionLabel={elem => {
                            const url = `https://leanny.github.io/splat3/images/skill/${elem.value}.png`
                            return (
                                <div className={classes.Entry}>
                                    {elem.value !== "None" ? (
                                        <img src={url} alt={elem.label} className={classes.Ability} />
                                    ) : (
                                        ""
                                    )}
                                    <span className={classes.Gap}> {elem.label}</span>
                                </div>
                            )
                        }}
                    />
                </div>
            </div>
            <div className={classes.InputWrapper}>
                <span className={classes.Title}>Target</span>
                <div className={classes.Input}>
                    <div style={props.showAbilityControls === 0 ? { border: "2px solid black" } : null}>
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${
                                ability_list_rev[props.selectedAbilities[0]]
                            }.png`}
                            width={32}
                            className={classes.Ability}
                            alt={ability_list_rev[props.selectedAbilities[0]]}
                            onClick={() => props.abilityControlChange(0)}
                        />
                    </div>
                    <div style={props.showAbilityControls === 1 ? { border: "2px solid black" } : null}>
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${
                                ability_list_rev[props.selectedAbilities[1]]
                            }.png`}
                            width={32}
                            className={classes.Ability}
                            alt={ability_list_rev[props.selectedAbilities[1]]}
                            onClick={() => props.abilityControlChange(1)}
                        />
                    </div>
                    <div style={props.showAbilityControls === 2 ? { border: "2px solid black" } : null}>
                        <img
                            src={`https://leanny.github.io/splat3/images/skill/${
                                ability_list_rev[props.selectedAbilities[2]]
                            }.png`}
                            width={32}
                            className={classes.Ability}
                            alt={ability_list_rev[props.selectedAbilities[2]]}
                            onClick={() => props.abilityControlChange(2)}
                        />
                    </div>
                </div>
            </div>

            {props.showAbilityControls >= 0 ? (
                <div className={classes.extraZone}>
                    <div className={classes.abilitiyList}>
                        {Object.keys(ability_list)
                            .filter(elem => ability_list[elem] < 100 || ability_list[elem] === 255)
                            .map((ability, idx) => {
                                return (
                                    <div
                                        key={idx}
                                        onClick={() =>
                                            props.abilityChange(props.showAbilityControls, ability_list[ability])
                                        }
                                    >
                                        <img
                                            src={`https://leanny.github.io/splat3/images/skill/${ability}.png`}
                                            className={classes.Ability}
                                            alt={ability}
                                        />
                                    </div>
                                )
                            })}
                    </div>
                </div>
            ) : null}
            <div className={classes.InputWrapper}>
                <Input
                    type="checkbox"
                    title="Allow Chunks"
                    val={props.allowChunks}
                    changed={props.allowChunksHandler}
                />
            </div>
            <div>
                <button className={classes.Button} onClick={props.calculateResults}>
                    Find Result
                </button>
            </div>
        </div>
    )
}

export default React.memo(ControlsSearch)
