    3: "HumanMove_Up",
    4: "SquidMove_Up",
    5: "SpecialIncrease_Up",
    6: "RespawnSpecialGauge_Save",
    7: "SpecialTime_Up",
    8: "RespawnTime_Save",
    9: "JumpTime_Save",
    10: "BombDistance_Up",
    11: "OpInkEffect_Reduction",
    12: "SubEffect_Reduction",
    13: "Action_Up",
    100: "StartAllUp",
    101: "EndAllUp",
    102: "MinorityUp",
