import classes from "./Usersettings.module.css"
import { faClipboard, faUpload, faDownload, faTrash, faFileImport, faFileCode } from "@fortawesome/free-solid-svg-icons"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { useLocalStorage } from "../../Utils"
import React, { useState, useCallback, useMemo } from "react"
import Modal from "../../components/UI/Modal/Modal"
import headData from "../../assets/json/parameters/GearInfoHead.json"
import clothesData from "../../assets/json/parameters/GearInfoClothes.json"
import shoesData from "../../assets/json/parameters/GearInfoShoes.json"
import SkillRev from "../../assets/json/parameters/SkillIds.json"

function useBoolState(initial = false) {
    const [bool, setBool] = useState(initial)
    const onTrue = useCallback(() => setBool(true), [setBool])
    const onFalse = useCallback(() => setBool(false), [setBool])
    return [bool, onTrue, onFalse]
}
const dict_length = d => Object.keys(d).length

const Usersettings = props => {
    const [gearList, setGearList] = useLocalStorage("gearList", {
        HaveGearHeadMap: {},
        HaveGearClothesMap: {},
        HaveGearShoesMap: {},
    })
    const [uploadOpen, onOpen, onClose] = useBoolState()

    const copyToClipboard = useCallback(
        () =>
            navigator.clipboard
                .writeText(JSON.stringify(gearList))
                .then(() => alert("Copied database to clipboard."))
                .catch(console.error),
        [gearList]
    )

    const deleteData = useCallback(() => {
        if (!window.confirm(`Are you sure you want to delete all data?`)) return

        setGearList({
            HaveGearHeadMap: {},
            HaveGearClothesMap: {},
            HaveGearShoesMap: {},
        })
    }, [setGearList])

    const downloadData = useCallback(() => {
        const date = new Date()
        const dateStr = date.toISOString().split(".")[0].replace("T", "_").replaceAll(":", "-")
        const JSONStr = JSON.stringify(gearList)
        const filename = `gear_seed_database_${dateStr}.json`
        const contentType = "application/json;charset=utf-8"
        const a = document.createElement("a")
        a.download = filename
        a.href = `data:${contentType},${encodeURIComponent(JSONStr)}`
        a.target = "_blank"
        document.body.appendChild(a)
        a.click()
        document.body.removeChild(a)
    }, [gearList])

    const onUpload = useCallback(
        newDB => {
            setGearList(newDB)
            onClose()
        },
        [onClose, setGearList]
    )

    const hasData =
        dict_length(gearList.HaveGearHeadMap) > 0 ||
        dict_length(gearList.HaveGearClothesMap) > 0 ||
        dict_length(gearList.HaveGearShoesMap) > 0 > 0

    const headSeeds = Object.assign(
        {},
        ...Object.entries(gearList.HaveGearHeadMap)
            .filter(([k, v]) => v.RandomContext > 0)
            .map(([k, v]) => ({ [k]: v }))
    )
    const clothesSeeds = Object.assign(
        {},
        ...Object.entries(gearList.HaveGearClothesMap)
            .filter(([k, v]) => v.RandomContext > 0)
            .map(([k, v]) => ({ [k]: v }))
    )
    const shoesSeeds = Object.assign(
        {},
        ...Object.entries(gearList.HaveGearShoesMap)
            .filter(([k, v]) => v.RandomContext > 0)
            .map(([k, v]) => ({ [k]: v }))
    )

    const foundSeeds = dict_length(headSeeds) + dict_length(clothesSeeds) + dict_length(shoesSeeds)

    return (
        <>
            <h2>Settings</h2>
            <div className={classes.Wrapper}>
                <div className={classes.cardWrapperWrapper}>
                    <div className={classes.cardWrapper}>
                        <div className={classes.Subtitle}>
                            <h6 className={classes.Subtitleh6}>Database</h6>
                        </div>
                        <hr className={classes.Divider} />
                        <div className={classes.CardContent}>
                            <div className={classes.CardDark}>
                                <div>
                                    <div>
                                        <p className={classes.TextDescription}>
                                            Head Gear: <strong>{dict_length(gearList.HaveGearHeadMap)}</strong>
                                        </p>
                                        <p className={classes.TextDescription}>
                                            Clothes: <strong>{dict_length(gearList.HaveGearClothesMap)}</strong>
                                        </p>
                                        <p className={classes.TextDescription}>
                                            Shoes: <strong>{dict_length(gearList.HaveGearShoesMap)}</strong>
                                        </p>
                                        <p className={classes.TextDescription}>
                                            Seeds Found: <strong>{foundSeeds}</strong>
                                        </p>
                                    </div>
                                    <div style={{ marginTop: 16 }}>
                                        <div className={classes.GridStyle}>
                                            <div className={classes.GridElem}>
                                                <button
                                                    disabled={!hasData}
                                                    onClick={copyToClipboard}
                                                    className={hasData ? classes.ButtonInfo : classes.ButtonDisabled}
                                                >
                                                    <FontAwesomeIcon icon={faClipboard} />{" "}
                                                    <span style={{ padding: 6 }}>Clipboard</span>
                                                </button>
                                            </div>
                                            <div className={classes.GridElem}>
                                                <Modal show={uploadOpen} modalClosed={onClose} wide={true}>
                                                    <UploadCard onReplace={onUpload} />
                                                </Modal>
                                                <button onClick={onOpen} className={classes.ButtonInfo}>
                                                    <FontAwesomeIcon icon={faUpload} />{" "}
                                                    <span style={{ padding: 6 }}>Upload</span>
                                                </button>
                                            </div>
                                            <div className={classes.GridElem}>
                                                <button
                                                    disabled={!hasData}
                                                    onClick={downloadData}
                                                    className={hasData ? classes.ButtonDefault : classes.ButtonDisabled}
                                                >
                                                    <FontAwesomeIcon icon={faDownload} />{" "}
                                                    <span style={{ padding: 6 }}>Download</span>
                                                </button>
                                            </div>
                                            <div className={classes.GridElem}>
                                                <button
                                                    disabled={!hasData}
                                                    onClick={deleteData}
                                                    className={hasData ? classes.ButtonError : classes.ButtonDisabled}
                                                >
                                                    <FontAwesomeIcon icon={faTrash} />{" "}
                                                    <span style={{ padding: 6 }}>Delete</span>
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

const UploadCard = props => {
    const [data, setdata] = useState("")
    const [filename, setfilename] = useState("")
    const [errorMsg, setErrorMsg] = useState("")

    const onUpload = async e => {
        const file = e.target.files[0]
        e.target.value = null // reset the value so the same file can be uploaded again...
        if (file) setfilename(file.name)
        const reader = new FileReader()
        reader.onload = () => setdata(reader.result)
        reader.readAsText(file)
    }

    const importedDatabase = useMemo(() => {
        if (!data) return
        let parsed
        try {
            parsed = JSON.parse(data)
            if (typeof parsed !== "object") {
                setErrorMsg("Unable to parse JSON file.")
                return
            }
        } catch (e) {
            setErrorMsg("Unable to parse JSON file.")
            return
        }
        let tmp_data = parsed
        if (Array.isArray(parsed)) {
            if (parsed.length === 2) {
                tmp_data = parsed[1]
            } else {
                setErrorMsg("Invalid Format.")
                return
            }
        }
        let head, clothes, shoes
        if (
            !("HaveGearHeadMap" in tmp_data) ||
            !("HaveGearClothesMap" in tmp_data) ||
            !("HaveGearShoesMap" in tmp_data)
        ) {
            setErrorMsg("Invalid Format.")
            return
        }
        head = tmp_data.HaveGearHeadMap
        clothes = tmp_data.HaveGearClothesMap
        shoes = tmp_data.HaveGearShoesMap

        const use_head = {}
        const use_clothes = {}
        const use_shoes = {}
        for (const [key, val] of Object.entries(head)) {
            const h = headData.filter(e => e.Id === parseInt(key))
            if (h.length > 0) {
                use_head[key] = {
                    ExSkillArray: val.ExSkillArray ?? [],
                    ExDrinksArray: val.ExDrinksArray ?? [],
                    MainSkill: val.MainSkill ?? SkillRev[h.Skill],
                    RandomContext: val.RandomContext ?? 0,
                }
            }
        }
        for (const [key, val] of Object.entries(clothes)) {
            const h = clothesData.filter(e => e.Id === parseInt(key))
            if (h.length > 0) {
                use_clothes[key] = {
                    ExSkillArray: val.ExSkillArray ?? [],
                    ExDrinksArray: val.ExDrinksArray ?? [],
                    MainSkill: val.MainSkill ?? SkillRev[h.Skill],
                    RandomContext: val.RandomContext ?? 0,
                }
            }
        }
        for (const [key, val] of Object.entries(shoes)) {
            const h = shoesData.filter(e => e.Id === parseInt(key))
            if (h.length > 0) {
                use_shoes[key] = {
                    ExSkillArray: val.ExSkillArray ?? [],
                    ExDrinksArray: val.ExDrinksArray ?? [],
                    MainSkill: val.MainSkill ?? SkillRev[h.Skill],
                    RandomContext: val.RandomContext ?? 0,
                }
            }
        }
        setErrorMsg("")
        return {
            HaveGearHeadMap: use_head,
            HaveGearClothesMap: use_clothes,
            HaveGearShoesMap: use_shoes,
        }
    }, [data])

    const replaceDB = useCallback(() => {
        if (!importedDatabase) return
        setdata("")
        setfilename("")
        props.onReplace(importedDatabase)
    }, [importedDatabase, props])

    return (
        <>
            <div className={classes.CardContent}>Database Upload</div>
            <div className={classes.CardContent}>
                <div className={classes.GridStyle}>
                    <div style={{ paddingLeft: 16, paddingTop: 16 }}>
                        <label>
                            <input
                                id="icon-button-file"
                                accept=".json"
                                type="file"
                                style={{ display: "none" }}
                                onChange={onUpload}
                            />

                            <span className={classes.ButtonDefault}>
                                <FontAwesomeIcon icon={faFileImport} style={{ marginRight: 8 }} />
                                Open
                            </span>
                        </label>
                    </div>
                    <div style={{ paddingLeft: 16, paddingTop: 16 }}>
                        <div style={{ paddingLeft: 16, paddingTop: 6 }}>
                            {filename ? (
                                <span>
                                    <FontAwesomeIcon icon={faFileCode} style={{ marginRight: 8 }} /> {filename}
                                </span>
                            ) : (
                                <span>Load your JSON file here...</span>
                            )}
                        </div>
                    </div>
                </div>
                <div></div>
                <span className={classes.TextTiny}>...or paste it here.</span>
                <textarea value={data} onChange={e => setdata(e.target.value)}></textarea>
                {!importedDatabase ? (
                    errorMsg
                ) : (
                    <div style={{ marginTop: 16 }}>
                        <div>
                            <div>
                                Head Gear: <strong>{dict_length(importedDatabase.HaveGearHeadMap)}</strong>
                            </div>
                            <div>
                                Clothes: <strong>{dict_length(importedDatabase.HaveGearClothesMap)}</strong>
                            </div>
                            <div>
                                Shoes: <strong>{dict_length(importedDatabase.HaveGearShoesMap)}</strong>
                            </div>
                        </div>
                    </div>
                )}
            </div>
            <hr className={classes.Divider} />
            <div className={classes.CardContent}>
                <button
                    className={importedDatabase ? classes.ButtonInfo : classes.ButtonDisabled}
                    disabled={!importedDatabase}
                    onClick={replaceDB}
                >
                    <FontAwesomeIcon icon={faUpload} /> <span style={{ padding: 6 }}>Update Database</span>
                </button>
            </div>
        </>
    )
}

export default Usersettings
