import React from "react"
import classes from "./Input.module.css"

const input = props => {
    return (
        <div className={classes.Wrapper}>
            <span className={classes.Title}>{props.title}</span>
            {props.type === undefined ? (
                <input className={classes.Input} onChange={props.changed} value={props.val} />
            ) : props.type === "checkbox" ? (
                <div className={classes.Input}>
                    <input type={props.type} onChange={props.changed} checked={props.val} />
                </div>
            ) : (
                <input type={props.type} className={classes.Input} onChange={props.changed} value={props.val} />
            )}
        </div>
    )
}

export default input
